








































import Vue from 'vue';

function sleep(ms: number) {
  return {
    isSleep: false,
    stop() {
      this.isSleep = false;
    },
    delay() {
      this.isSleep = true;
      setTimeout(() => {
        this.isSleep = false;
      }, ms);
    },
  };
}

export default Vue.extend({
  name: '',
  data() {
    return {
      canvasSize: {
        width: 375,
        height: 400,
        padding: 40,
        grid: 50,
      },
      delayFn: sleep(60),
    };
  },
  mounted() {
    this.drawWebCartesian();
  },
  methods: {
    async drawWebCartesian() {
      if (this.delayFn.isSleep) {
        return;
      }
      this.delayFn.delay();
      await this.$nextTick();
      const canvas = this.$refs.WebCartesian as HTMLCanvasElement;
      const ctx = canvas.getContext('2d');
      const {
        padding, width, height, grid,
      } = this.canvasSize;
      const point = {
        x: 0 + padding,
        y: 0 + padding,
      };
      if (ctx) {
        ctx.clearRect(0, 0, width, height);
        ctx.lineWidth = 1;
        ctx.strokeStyle = '#000';
        // draw x axis
        ctx.beginPath();
        ctx.moveTo(point.x, point.y);
        ctx.lineTo(width - padding, point.y);
        ctx.stroke();
        // draw y axis
        ctx.beginPath();
        ctx.moveTo(point.x, point.y);
        ctx.lineTo(point.x, height - padding);
        ctx.stroke();
        // draw grid
        ctx.beginPath();
        ctx.lineWidth = 0.5;
        ctx.strokeStyle = '#ccc';
        for (let i = padding; i < (width - padding); i += grid) {
          ctx.moveTo(i, point.y);
          ctx.lineTo(i, height - padding);
          ctx.font = '12px Arial';
          ctx.fillStyle = '#000';
          ctx.fillText(`${i - padding}`, i, padding);
        }
        ctx.stroke();

        ctx.beginPath();
        for (let i = padding; i < (height - padding); i += grid) {
          ctx.moveTo(point.x, i);
          ctx.lineTo(width - padding, i);
          ctx.font = '12px Arial';
          ctx.fillStyle = '#0cc';
          ctx.fillText(`${i - padding}`, padding - 20, i);
        }
        ctx.stroke();
      }
    },
  },
});
